import { defineStore, storeToRefs } from 'pinia';
import { computed, ComputedRef, ref, Ref } from 'vue';
import { useCategoriesStore } from '@/stores/categories';
import { useTransactionsStore } from '@/stores/transactions';
import { useDbStore } from '@/stores/database';
import { onValue, push, set } from 'firebase/database';
import { ref as refDb } from '@firebase/database';
import { db } from '@/utils/firebase.config';
import PINIA from '@/stores/index';

const { categoriesAvailableForLimitation } = storeToRefs(useCategoriesStore(PINIA));
const { outcomeTransactionsInThisMonth } = storeToRefs(useTransactionsStore(PINIA));

export type LimitData = { categoryId: string, limit: number, id: string };
export const usePlanningStore = defineStore('limits', () => {
  const limitsData: Ref<LimitData[]> = ref([]);
  const today: Ref<Date> = ref(new Date());
  const setLimitValue: Ref<number> = ref(0);
  const counter: Ref<number> = ref(0);

  const limits: ComputedRef<LimitData[]> = computed(() => {
    return Object.values(limitsData.value);
  });

  const pushNewLimit = async (data: LimitData) => {
    const { baseId } = storeToRefs(useDbStore(PINIA));

    try {
      const newLimitRef = push(refDb(db, `bases/${ baseId.value }/limits/`));
      if (typeof newLimitRef.key === 'string') {
        data.id = newLimitRef.key;
      }
      await set(newLimitRef, data);
    } catch (e) {
      console.error(e);
    }
  };

  const subscribeToLimits = async () => {
    try {
      const { baseId } = storeToRefs(useDbStore(PINIA));
      const limitsRef = refDb(db, `bases/${ baseId.value }/limits/`);
      onValue(limitsRef, async (snapshot) => {
        if (snapshot.exists()) {
          limitsData.value = snapshot.val();
        } else if (counter.value < 5) {
          setTimeout(() => {
            counter.value++;
            subscribeToLimits();
          }, (counter.value + 1) * 100);
        } else {
          limitsData.value = [];
          console.error('    ');
        }
      });
    } catch (e) {
      console.error(e);
    }
  };

  return {
    setLimitValue,
    today,
    limits,
    pushNewLimit,
    subscribeToLimits
  };
});